@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@300;400;500;600;700;800&family=Public+Sans:wght@300;400;500;600;700&family=JetBrains+Mono:wght@400;500;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

/* ========================================================================
   SHARDA HR — PREMIUM DESIGN SYSTEM
   Inspired by Eclipticon: depth, motion, sophistication
   ======================================================================== */

@layer base {
    :root {
        --background: 220 20% 97%;
        --foreground: 222.2 84% 4.9%;
        --card: 0 0% 100%;
        --card-foreground: 222.2 84% 4.9%;
        --popover: 0 0% 100%;
        --popover-foreground: 222.2 84% 4.9%;
        --primary: 243 75% 59%;
        --primary-foreground: 0 0% 100%;
        --secondary: 220 14% 96%;
        --secondary-foreground: 222.2 47.4% 11.2%;
        --muted: 220 14% 96%;
        --muted-foreground: 220 9% 46%;
        --accent: 24 95% 53%;
        --accent-foreground: 0 0% 100%;
        --destructive: 0 84.2% 60.2%;
        --destructive-foreground: 0 0% 98%;
        --border: 220 13% 91%;
        --input: 220 13% 91%;
        --ring: 243 75% 59%;
        --radius: 0.625rem;
        --chart-1: 243 75% 59%;
        --chart-2: 160 84% 39%;
        --chart-3: 24 95% 53%;
        --chart-4: 280 65% 60%;
        --chart-5: 190 80% 50%;
        --sidebar-bg: 222.2 84% 4.9%;
        --sidebar-foreground: 210 40% 98%;
        --sidebar-accent: 217.2 32.6% 17.5%;
        --sidebar-muted: 215 20.2% 65.1%;
    }
}

@layer base {
    * { @apply border-border; }

    html {
        scroll-behavior: smooth;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }

    body {
        @apply text-foreground;
        font-family: 'Public Sans', -apple-system, BlinkMacSystemFont, sans-serif;
        background: hsl(225 20% 96%);
        color: hsl(222.2 84% 4.9%);
    }

    h1, h2, h3, h4, h5, h6 {
        font-family: 'Manrope', -apple-system, BlinkMacSystemFont, sans-serif;
        font-weight: 700;
        letter-spacing: -0.015em;
    }
}

/* ========================================================================
   SCROLLBAR
   ======================================================================== */
::-webkit-scrollbar { width: 5px; height: 5px; }
::-webkit-scrollbar-track { background: transparent; }
::-webkit-scrollbar-thumb { background: hsl(var(--muted-foreground) / 0.15); border-radius: 10px; }
::-webkit-scrollbar-thumb:hover { background: hsl(var(--muted-foreground) / 0.3); }

/* ========================================================================
   GLOBAL COMPONENT OVERRIDES — makes EVERYTHING premium automatically
   ======================================================================== */

/* ------- CARDS — Light Glass Morphism with Depth ------- */
[data-slot="card"],
.rounded-xl.border.bg-card {
    background: hsl(0 0% 100% / 0.65);
    backdrop-filter: blur(24px) saturate(180%);
    -webkit-backdrop-filter: blur(24px) saturate(180%);
    border: 1px solid hsl(220 20% 90% / 0.6);
    border-radius: 16px;
    box-shadow:
        0 1px 2px hsl(220 40% 10% / 0.04),
        0 4px 16px hsl(220 40% 10% / 0.06),
        inset 0 1px 0 hsl(0 0% 100% / 0.8);
    transition: all 0.35s cubic-bezier(0.4, 0, 0.2, 1);
}

[data-slot="card"]:hover,
.rounded-xl.border.bg-card:hover {
    background: hsl(0 0% 100% / 0.8);
    border-color: hsl(var(--primary) / 0.15);
    box-shadow:
        0 4px 8px hsl(220 40% 10% / 0.06),
        0 12px 40px hsl(220 40% 10% / 0.1),
        0 0 0 1px hsl(var(--primary) / 0.05),
        inset 0 1px 0 hsl(0 0% 100% / 0.9);
    transform: translateY(-3px);
}

/* ------- BUTTONS — Bold with Glow ------- */
button, [role="button"] {
    transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1) !important;
}

button:active:not(:disabled), [role="button"]:active:not(:disabled) {
    transform: scale(0.95);
}

/* Primary buttons get neon glow */
.bg-primary {
    box-shadow: 0 0 20px hsl(var(--primary) / 0.3), 0 4px 12px hsl(var(--primary) / 0.2);
}

.bg-primary:hover {
    box-shadow: 0 0 30px hsl(var(--primary) / 0.4), 0 8px 24px hsl(var(--primary) / 0.3);
    transform: translateY(-2px);
}

/* Outline buttons — refined glass */
.border.border-input {
    transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}

.border.border-input:hover {
    border-color: hsl(var(--primary) / 0.3);
    background: hsl(var(--primary) / 0.04);
    box-shadow: 0 0 12px hsl(var(--primary) / 0.08);
}

/* ------- INPUTS — Light Glass ------- */
input:not([type="checkbox"]):not([type="radio"]):not([type="range"]),
textarea,
select,
[role="combobox"] {
    transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1) !important;
    border-radius: 10px !important;
}

input:focus:not([type="checkbox"]):not([type="radio"]),
textarea:focus,
[data-state="open"][role="combobox"] {
    box-shadow: 0 0 0 3px hsl(var(--primary) / 0.1), 0 0 20px hsl(var(--primary) / 0.06) !important;
    border-color: hsl(var(--primary) / 0.5) !important;
}

/* ------- DIALOGS / MODALS — Glass with Depth ------- */
[data-state="open"][role="dialog"],
[data-state="open"][data-slot="dialog-content"] {
    animation: dialogOpen 0.35s cubic-bezier(0.4, 0, 0.2, 1) both;
    border-radius: 20px;
    background: hsl(0 0% 100% / 0.85) !important;
    backdrop-filter: blur(40px) saturate(180%);
    border: 1px solid hsl(220 20% 90% / 0.6);
    box-shadow:
        0 32px 80px hsl(220 40% 10% / 0.15),
        0 0 60px hsl(var(--primary) / 0.04),
        inset 0 1px 0 hsl(0 0% 100% / 0.9);
}

@keyframes dialogOpen {
    from { opacity: 0; transform: scale(0.96) translateY(8px); }
    to { opacity: 1; transform: scale(1) translateY(0); }
}

/* Dialog overlay */
[data-state="open"][data-slot="dialog-overlay"] {
    backdrop-filter: blur(8px);
    background: hsl(220 40% 10% / 0.35);
}

/* ------- BADGE ------- */
[data-slot="badge"],
.inline-flex.items-center.rounded-md.border {
    border-radius: 6px;
    font-weight: 600;
    letter-spacing: 0.02em;
}

/* ------- TABS — Glass ------- */
[role="tablist"] {
    background: hsl(0 0% 100% / 0.5);
    backdrop-filter: blur(12px);
    border-radius: 12px;
    padding: 3px;
    border: 1px solid hsl(220 13% 91% / 0.5);
}

[role="tab"][data-state="active"] {
    background: white;
    box-shadow: 0 2px 8px hsl(220 40% 10% / 0.08), 0 0 0 1px hsl(var(--primary) / 0.08);
    border-radius: 9px;
}

[role="tab"] {
    border-radius: 9px;
    transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);
    font-weight: 500;
}

[role="tab"]:hover:not([data-state="active"]) {
    background: hsl(220 14% 96% / 0.8);
}

/* ------- DROPDOWN MENUS — Glass ------- */
[role="menu"] {
    border-radius: 14px;
    background: hsl(0 0% 100% / 0.85) !important;
    backdrop-filter: blur(40px) saturate(180%);
    box-shadow:
        0 16px 48px hsl(220 40% 10% / 0.12),
        inset 0 1px 0 hsl(0 0% 100% / 0.9);
    border: 1px solid hsl(220 13% 91% / 0.6);
    animation: dropdownOpen 0.2s cubic-bezier(0.4, 0, 0.2, 1) both;
}

@keyframes dropdownOpen {
    from { opacity: 0; transform: translateY(-4px) scale(0.98); }
    to { opacity: 1; transform: translateY(0) scale(1); }
}

[role="menuitem"] {
    border-radius: 8px;
    transition: all 0.15s ease;
}

[role="menuitem"]:hover {
    background: hsl(var(--primary) / 0.06) !important;
}

/* ------- SELECT DROPDOWNS — Glass ------- */
[role="listbox"] {
    border-radius: 14px;
    background: hsl(0 0% 100% / 0.9) !important;
    backdrop-filter: blur(40px) saturate(180%);
    box-shadow: 0 16px 48px hsl(220 40% 10% / 0.12);
    border: 1px solid hsl(220 13% 91% / 0.6);
    animation: dropdownOpen 0.2s cubic-bezier(0.4, 0, 0.2, 1) both;
}

[role="option"]:hover, [role="option"][data-highlighted] {
    background: hsl(var(--primary) / 0.06) !important;
}

/* ------- TOOLTIPS ------- */
[role="tooltip"] {
    border-radius: 8px;
    animation: fadeIn 0.15s ease both;
    box-shadow: 0 8px 24px hsl(220 40% 10% / 0.12);
}

/* ------- TABLES — Light with Depth ------- */
table {
    border-collapse: separate;
    border-spacing: 0;
}

thead th {
    font-family: 'Manrope', sans-serif;
    font-weight: 600;
    font-size: 0.7rem;
    text-transform: uppercase;
    letter-spacing: 0.06em;
    color: hsl(220 9% 46%);
    background: hsl(220 20% 97% / 0.8);
    border-bottom: 1px solid hsl(220 13% 91%);
}

tbody tr {
    transition: all 0.2s ease;
}

tbody tr:hover {
    background: hsl(var(--primary) / 0.025);
}

tbody td {
    border-bottom: 1px solid hsl(220 13% 91% / 0.5);
}

/* ========================================================================
   LOADING SCREEN
   ======================================================================== */
.loading-screen {
    position: fixed;
    inset: 0;
    z-index: 9999;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: hsl(var(--sidebar-bg));
    gap: 32px;
}

.loading-arc { position: absolute; width: 400px; top: 50%; left: 50%; transform: translate(-50%, -80px); }
.loading-logo { display: flex; align-items: center; gap: 12px; z-index: 1; }
.loading-logo-img { width: 48px; height: 48px; object-fit: contain; filter: drop-shadow(0 0 20px hsl(var(--primary) / 0.5)); }
.loading-logo-text { font-family: 'Manrope', sans-serif; font-size: 1.75rem; font-weight: 700; color: white; letter-spacing: -0.02em; }
.loading-bar-track { width: 200px; height: 3px; background: hsl(var(--sidebar-accent)); border-radius: 2px; overflow: hidden; z-index: 1; }
.loading-bar-fill { height: 100%; background: linear-gradient(90deg, hsl(var(--primary)), hsl(var(--primary) / 0.6)); border-radius: 2px; box-shadow: 0 0 12px hsl(var(--primary) / 0.5); }

/* ========================================================================
   SIDEBAR
   ======================================================================== */
.sidebar {
    background: linear-gradient(180deg, hsl(222.2 84% 4.9%), hsl(222.2 84% 6%));
    color: hsl(var(--sidebar-foreground));
    position: relative;
    overflow: hidden;
}

.sidebar::before {
    content: '';
    position: absolute;
    inset: 0;
    background: radial-gradient(ellipse at 50% 0%, hsl(var(--primary) / 0.08) 0%, transparent 60%);
    pointer-events: none;
}

.sidebar-link {
    @apply flex items-center gap-3 px-3 py-2.5 rounded-lg text-sm font-medium;
    color: hsl(var(--sidebar-muted));
    position: relative;
    transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}

.sidebar-link:hover {
    background-color: hsl(var(--sidebar-accent) / 0.8);
    color: hsl(var(--sidebar-foreground));
    transform: translateX(2px);
}

.sidebar-link.active {
    background: linear-gradient(135deg, hsl(var(--primary)), hsl(var(--primary) / 0.85));
    color: hsl(var(--primary-foreground));
    box-shadow: 0 4px 16px hsl(var(--primary) / 0.3), inset 0 1px 0 hsl(0 0% 100% / 0.1);
}

.sidebar-link.active::before {
    content: '';
    position: absolute;
    left: -12px;
    top: 50%;
    transform: translateY(-50%);
    width: 4px;
    height: 60%;
    background: hsl(var(--primary));
    border-radius: 0 2px 2px 0;
    box-shadow: 0 0 8px hsl(var(--primary) / 0.5);
}

/* ========================================================================
   ANIMATIONS
   ======================================================================== */
@keyframes fadeIn { from { opacity: 0; } to { opacity: 1; } }

@keyframes slideUp {
    from { opacity: 0; transform: translateY(20px); }
    to { opacity: 1; transform: translateY(0); }
}

@keyframes slideInFromLeft {
    from { opacity: 0; transform: translateX(-16px); }
    to { opacity: 1; transform: translateX(0); }
}

@keyframes scaleIn {
    from { opacity: 0; transform: scale(0.95); }
    to { opacity: 1; transform: scale(1); }
}

@keyframes shimmer {
    0% { background-position: -200% 0; }
    100% { background-position: 200% 0; }
}

@keyframes lineExpand {
    from { width: 0; }
    to { width: 100%; }
}

@keyframes confettiFall {
    0% { transform: translateY(-100%) rotate(0deg); opacity: 1; }
    100% { transform: translateY(100vh) rotate(720deg); opacity: 0; }
}

.animate-fade-in { animation: fadeIn 0.4s cubic-bezier(0.4, 0, 0.2, 1) both; }
.animate-slide-up { animation: slideUp 0.5s cubic-bezier(0.4, 0, 0.2, 1) both; }

/* Staggered children */
.stagger-children > * { animation: slideUp 0.5s cubic-bezier(0.4, 0, 0.2, 1) both; }
.stagger-children > *:nth-child(1) { animation-delay: 0ms; }
.stagger-children > *:nth-child(2) { animation-delay: 60ms; }
.stagger-children > *:nth-child(3) { animation-delay: 120ms; }
.stagger-children > *:nth-child(4) { animation-delay: 180ms; }
.stagger-children > *:nth-child(5) { animation-delay: 240ms; }
.stagger-children > *:nth-child(6) { animation-delay: 300ms; }
.stagger-children > *:nth-child(7) { animation-delay: 360ms; }
.stagger-children > *:nth-child(8) { animation-delay: 420ms; }

/* ========================================================================
   SECTION PILL  // HEADING
   ======================================================================== */
.section-pill {
    @apply inline-flex items-center px-3 py-1 rounded-full text-xs font-semibold uppercase;
    background: hsl(var(--primary) / 0.06);
    color: hsl(var(--primary));
    border: 1px solid hsl(var(--primary) / 0.12);
    font-family: 'JetBrains Mono', monospace;
    letter-spacing: 0.08em;
    font-size: 0.65rem;
    box-shadow: 0 0 12px hsl(var(--primary) / 0.06);
}

.mono-accent {
    font-family: 'JetBrains Mono', monospace;
    letter-spacing: 0.04em;
}

/* ========================================================================
   HEADER ACCENT LINE
   ======================================================================== */
.header-accent-line {
    height: 2px;
    background: linear-gradient(90deg, hsl(var(--primary)), hsl(var(--primary) / 0.2), transparent);
    animation: lineExpand 0.6s cubic-bezier(0.4, 0, 0.2, 1) both;
    border-radius: 1px;
}

/* ========================================================================
   GLASS MORPHISM
   ======================================================================== */
.glass {
    background: hsl(0 0% 100% / 0.6);
    backdrop-filter: blur(24px) saturate(180%);
    -webkit-backdrop-filter: blur(24px) saturate(180%);
    border: 1px solid hsl(220 13% 91% / 0.5);
}

/* ========================================================================
   NOISE BACKGROUND
   ======================================================================== */
.noise-bg::after {
    content: '';
    position: fixed;
    inset: 0;
    z-index: 0;
    opacity: 0.018;
    pointer-events: none;
    background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 256 256' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='n'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.9' numOctaves='4' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23n)' opacity='1'/%3E%3C/svg%3E");
    background-repeat: repeat;
    background-size: 256px 256px;
}

/* ========================================================================
   PREMIUM STAT CARDS (for calendar, dashboard, etc.)
   ======================================================================== */
.premium-stat {
    @apply relative overflow-hidden rounded-xl border p-4;
    background: hsl(0 0% 100% / 0.6);
    backdrop-filter: blur(16px);
    transition: all 0.35s cubic-bezier(0.4, 0, 0.2, 1);
    box-shadow: 0 2px 8px hsl(220 40% 10% / 0.05), inset 0 1px 0 hsl(0 0% 100% / 0.8);
}

.premium-stat:hover {
    transform: translateY(-3px);
    box-shadow: 0 12px 32px hsl(220 40% 10% / 0.1);
    background: hsl(0 0% 100% / 0.8);
}

.premium-stat::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    border-radius: 3px 3px 0 0;
}

.premium-stat.stat-emerald::before { background: linear-gradient(90deg, #10b981, #34d399); }
.premium-stat.stat-blue::before { background: linear-gradient(90deg, #3b82f6, #60a5fa); }
.premium-stat.stat-amber::before { background: linear-gradient(90deg, #f59e0b, #fbbf24); }
.premium-stat.stat-yellow::before { background: linear-gradient(90deg, #eab308, #facc15); }
.premium-stat.stat-purple::before { background: linear-gradient(90deg, #8b5cf6, #a78bfa); }
.premium-stat.stat-indigo::before { background: linear-gradient(90deg, #6366f1, #818cf8); }
.premium-stat.stat-rose::before { background: linear-gradient(90deg, #f43f5e, #fb7185); }
.premium-stat.stat-slate::before { background: linear-gradient(90deg, #64748b, #94a3b8); }

/* ========================================================================
   PREMIUM CALENDAR
   ======================================================================== */
.premium-cal-grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 4px;
}

.premium-cal-header {
    @apply text-center py-2.5 text-xs font-bold uppercase tracking-wider;
    color: hsl(220 9% 50%);
    font-family: 'Manrope', sans-serif;
}

.premium-cal-header.sunday { color: hsl(0 70% 55%); }

.premium-cal-cell {
    @apply relative rounded-xl p-2 cursor-pointer;
    min-height: 96px;
    background: white;
    border: 1px solid hsl(220 13% 93%);
    transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}

.premium-cal-cell:hover {
    border-color: hsl(var(--primary) / 0.25);
    box-shadow: 0 4px 16px hsl(220 40% 10% / 0.06);
    transform: translateY(-1px);
}

.premium-cal-cell.today {
    border-color: hsl(var(--primary));
    box-shadow:
        0 0 0 2px hsl(var(--primary) / 0.12),
        0 4px 16px hsl(var(--primary) / 0.08);
}

.premium-cal-cell.today .cal-date-num {
    background: hsl(var(--primary));
    color: white;
    border-radius: 8px;
    padding: 2px 8px;
}

.premium-cal-cell.inactive {
    opacity: 0.3;
    pointer-events: none;
}

.premium-cal-cell.present { background: hsl(160 84% 39% / 0.04); border-color: hsl(160 84% 39% / 0.15); }
.premium-cal-cell.wfh { background: hsl(217 91% 60% / 0.04); border-color: hsl(217 91% 60% / 0.15); }
.premium-cal-cell.leave { background: hsl(38 92% 50% / 0.04); border-color: hsl(38 92% 50% / 0.15); }
.premium-cal-cell.holiday { background: hsl(271 76% 53% / 0.04); border-color: hsl(271 76% 53% / 0.15); }
.premium-cal-cell.absent { background: hsl(0 84% 60% / 0.03); border-color: hsl(0 84% 60% / 0.12); }
.premium-cal-cell.sunday { background: hsl(220 14% 96% / 0.5); }

.cal-date-num {
    @apply text-sm font-semibold;
    transition: all 0.2s ease;
}

.cal-event-chip {
    @apply text-[10px] px-1.5 py-0.5 rounded-md truncate flex items-center gap-1;
    font-weight: 500;
}

.cal-event-chip.task { background: hsl(271 76% 53% / 0.08); color: hsl(271 76% 53%); }
.cal-event-chip.meeting { background: hsl(217 91% 60% / 0.08); color: hsl(217 91% 60%); }
.cal-event-chip.holiday-chip { background: hsl(271 76% 53% / 0.1); color: hsl(271 76% 40%); }
.cal-event-chip.leave-chip { background: hsl(38 92% 50% / 0.1); color: hsl(38 70% 35%); }
.cal-event-chip.celebration { background: hsl(340 82% 52% / 0.08); color: hsl(340 82% 40%); }

/* ========================================================================
   PREMIUM LEGEND
   ======================================================================== */
.premium-legend {
    @apply flex items-center gap-1.5 text-xs font-medium;
    color: hsl(220 9% 50%);
}

.premium-legend-dot {
    @apply w-2 h-2 rounded-full;
}

/* ========================================================================
   INTERACTIVE CARD
   ======================================================================== */
.interactive-card {
    @apply relative rounded-xl border bg-white p-6 cursor-pointer;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.interactive-card:hover {
    border-color: hsl(var(--primary) / 0.25);
    box-shadow: 0 8px 30px hsl(220 40% 10% / 0.08);
}

.interactive-card::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 3px;
    background: linear-gradient(180deg, hsl(var(--primary)), hsl(var(--primary) / 0.4));
    border-radius: 3px 0 0 3px;
    transform: scaleY(0);
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    transform-origin: center;
}

.interactive-card:hover::after { transform: scaleY(1); }

/* ========================================================================
   SKELETON LOADER
   ======================================================================== */
.skeleton {
    @apply rounded-lg;
    background: linear-gradient(90deg, hsl(220 14% 95%) 0%, hsl(220 14% 92%) 40%, hsl(220 14% 95%) 80%);
    background-size: 200% 100%;
    animation: shimmer 1.8s infinite ease-in-out;
}

/* ========================================================================
   EMPTY STATES
   ======================================================================== */
.empty-state { @apply flex flex-col items-center justify-center py-12 text-center; }
.empty-state-icon { @apply w-16 h-16 text-slate-200 mb-4; }
.empty-state-title { @apply text-base font-semibold text-slate-600 mb-1; }
.empty-state-description { @apply text-sm text-slate-400 max-w-sm; }

/* ========================================================================
   CELEBRATIONS
   ======================================================================== */
.celebration-birthday { background: linear-gradient(135deg, #fff7ed, #fef3c7, #fce7f3); border: 1px solid #fdba74; }
.celebration-anniversary { background: linear-gradient(135deg, #eff6ff, #dbeafe, #e0e7ff); border: 1px solid #93c5fd; }
.celebration-marriage { background: linear-gradient(135deg, #fdf2f8, #fce7f3, #fef2f2); border: 1px solid #f9a8d4; }
.confetti-piece { position: fixed; width: 8px; height: 8px; border-radius: 2px; animation: confettiFall 3s ease-in forwards; z-index: 100; pointer-events: none; }

/* ========================================================================
   GRADIENT TEXT
   ======================================================================== */
.gradient-text {
    background: linear-gradient(135deg, hsl(var(--primary)), hsl(24 95% 53%));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

/* ========================================================================
   PAGE TRANSITIONS — content feels alive
   ======================================================================== */
.page-enter { animation: slideUp 0.4s cubic-bezier(0.4, 0, 0.2, 1) both; }

/* ========================================================================
   ATTENDANCE CALENDAR SPECIFIC (dots instead of harsh colors)
   ======================================================================== */
.att-dot {
    @apply inline-block w-2 h-2 rounded-full;
}
.att-dot.present { background: #10b981; }
.att-dot.late { background: #f59e0b; }
.att-dot.absent { background: #ef4444; }


/* ========================================================================
   DYNAMIC CELEBRATION THEMES — Dashboard theming based on events
   ======================================================================== */
.celebration-theme {
    position: relative;
}
.celebration-theme::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    z-index: 50;
    transition: background 0.5s ease;
}

/* Birthday Theme — warm amber/gold accents */
.celebration-theme-birthday::before {
    background: linear-gradient(90deg, #f59e0b, #f97316, #f59e0b);
    animation: shimmerBar 3s ease-in-out infinite;
}
.celebration-theme-birthday .stat-card {
    border-top: 2px solid rgba(245, 158, 11, 0.3);
}
.celebration-theme-birthday .stat-card:hover {
    box-shadow: 0 4px 24px -4px rgba(245, 158, 11, 0.15);
}

/* Work Anniversary Theme — blue/indigo accents */
.celebration-theme-work_anniversary::before {
    background: linear-gradient(90deg, #3b82f6, #6366f1, #3b82f6);
    animation: shimmerBar 3s ease-in-out infinite;
}
.celebration-theme-work_anniversary .stat-card {
    border-top: 2px solid rgba(59, 130, 246, 0.3);
}
.celebration-theme-work_anniversary .stat-card:hover {
    box-shadow: 0 4px 24px -4px rgba(59, 130, 246, 0.15);
}

/* Marriage Anniversary Theme — rose/pink accents */
.celebration-theme-marriage_anniversary::before {
    background: linear-gradient(90deg, #ec4899, #f43f5e, #ec4899);
    animation: shimmerBar 3s ease-in-out infinite;
}
.celebration-theme-marriage_anniversary .stat-card {
    border-top: 2px solid rgba(236, 72, 153, 0.3);
}
.celebration-theme-marriage_anniversary .stat-card:hover {
    box-shadow: 0 4px 24px -4px rgba(236, 72, 153, 0.15);
}

/* Custom Event Theme — emerald/teal accents */
.celebration-theme-custom::before {
    background: linear-gradient(90deg, #10b981, #14b8a6, #10b981);
    animation: shimmerBar 3s ease-in-out infinite;
}
.celebration-theme-custom .stat-card {
    border-top: 2px solid rgba(16, 185, 129, 0.3);
}
.celebration-theme-custom .stat-card:hover {
    box-shadow: 0 4px 24px -4px rgba(16, 185, 129, 0.15);
}

@keyframes shimmerBar {
    0%, 100% { opacity: 1; }
    50% { opacity: 0.6; }
}

.confetti-piece {
    position: fixed;
    width: 8px;
    height: 8px;
    border-radius: 2px;
    z-index: 1000;
    pointer-events: none;
}


/* ========================================================================
   MOBILE & TABLET RESPONSIVE — Seamless across all devices
   ======================================================================== */

/* --- Safe area for Capacitor/notch devices --- */
:root {
    --safe-area-top: env(safe-area-inset-top, 0px);
    --safe-area-bottom: env(safe-area-inset-bottom, 0px);
}

body {
    padding-top: var(--safe-area-top);
    padding-bottom: var(--safe-area-bottom);
}

/* --- Tables: horizontal scroll on mobile --- */
@media (max-width: 768px) {
    table { display: block; }
    thead { display: block; }
    tbody { display: block; overflow-x: auto; -webkit-overflow-scrolling: touch; }
    tr { min-width: 600px; display: flex; }
    th, td { flex: 1; min-width: 100px; padding: 8px 10px !important; font-size: 0.8rem; }

    /* Wrap tables in scroll container */
    [data-slot="card"] table,
    .rounded-xl table {
        display: block;
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
    }
}

/* --- Dialogs: near full-screen on mobile --- */
@media (max-width: 640px) {
    [data-state="open"][role="dialog"],
    [data-state="open"][data-slot="dialog-content"] {
        width: calc(100vw - 16px) !important;
        max-width: calc(100vw - 16px) !important;
        max-height: 90vh !important;
        border-radius: 16px !important;
        margin: 8px !important;
    }

    /* Tabs: scrollable horizontal */
    [role="tablist"] {
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
        flex-wrap: nowrap !important;
        scrollbar-width: none;
    }
    [role="tablist"]::-webkit-scrollbar { display: none; }
    [role="tab"] {
        white-space: nowrap;
        flex-shrink: 0;
        font-size: 0.8rem;
        padding: 6px 12px !important;
    }

    /* Header compact */
    header .flex { gap: 4px; }
    header h1 { font-size: 1.2rem !important; }

    /* Section pill smaller */
    .section-pill { font-size: 0.55rem; padding: 2px 8px; }

    /* Cards: tighter padding */
    [data-slot="card"] { border-radius: 12px; }
    [data-slot="card-content"] { padding: 12px !important; }
    [data-slot="card-header"] { padding: 12px 12px 0 !important; }

    /* Buttons: touch-friendly size */
    button, [role="button"] { min-height: 40px; }

    /* Stat cards: 2-col grid */
    .grid-cols-4, .lg\:grid-cols-4, .sm\:grid-cols-2.lg\:grid-cols-4 {
        grid-template-columns: repeat(2, 1fr) !important;
    }

    /* Full-width inputs */
    input, textarea, select, [role="combobox"] {
        font-size: 16px !important; /* Prevents iOS zoom */
    }

    /* Badge smaller */
    [data-slot="badge"] { font-size: 0.65rem; padding: 1px 6px; }
}

/* --- Tablet tweaks (768px - 1024px) --- */
@media (min-width: 641px) and (max-width: 1024px) {
    [data-state="open"][role="dialog"],
    [data-state="open"][data-slot="dialog-content"] {
        max-width: 85vw !important;
        max-height: 85vh !important;
    }

    .grid-cols-4, .lg\:grid-cols-4 {
        grid-template-columns: repeat(2, 1fr) !important;
    }
}

/* --- Touch interaction improvements --- */
@media (hover: none) and (pointer: coarse) {
    /* Remove hover effects that don't work on touch */
    [data-slot="card"]:hover {
        transform: none;
    }

    /* Larger touch targets */
    [role="menuitem"] { padding: 10px 12px !important; }
    [role="option"] { padding: 10px 12px !important; }
    tbody tr { min-height: 48px; }

    /* Active state instead of hover */
    button:active, [role="button"]:active {
        transform: scale(0.97);
        opacity: 0.9;
    }

    [data-slot="card"]:active {
        transform: scale(0.99);
        box-shadow: 0 2px 8px hsl(220 40% 10% / 0.08);
    }
}

/* --- Capacitor specific: status bar padding --- */
.capacitor-app header {
    padding-top: var(--safe-area-top);
}

.capacitor-app .sidebar {
    padding-top: var(--safe-area-top);
}
